import React, { Fragment } from 'react';
import { Link } from 'gatsby';
import Spiral from './spiral';
import { rhythm, scale } from '../utils/typography';
import styles from './layout.module.css';

export default ({ location, children, ...props }) => {
  const rootPath = `${__PATH_PREFIX__}/`; // eslint-disable-line no-undef
  let header;
  const title = 'Miss Andi N. Fiziks';
  if (location.pathname === rootPath) {
    header = (
      <h1
        style={{
          ...scale(1.2),
          marginBottom: rhythm(1.5),
          marginTop: 0,
          color: '#004e2b',
        }}
        className={styles.main__header}
      >
        <Link
          style={{
            boxShadow: 'none',
            textDecoration: 'none',
            color: 'inherit',
          }}
          to="/"
        >
          {title}
        </Link>
      </h1>
    );
  } else {
    header = (
      <h3
        style={{
          fontFamily: 'Montserrat, sans-serif',
          marginTop: 0,
          marginBottom: rhythm(-1),
        }}
      >
        <Link
          style={{
            boxShadow: 'none',
            textDecoration: 'none',
            color: 'inherit',
          }}
          to="/"
        >
          {title}
        </Link>
      </h3>
    );
  }
  return (
    <Fragment>
      <Spiral parentNode={styles.outer_container} />
      <div className={styles.outer_container}>
        {header}
        {children}
      </div>
    </Fragment>
  );
};
